import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import { api } from '@/config/common.js'
import { queryFeeCategory, createFeeCategory, enableFeeCategory, disableFeeCategory } from '@/api/capfee.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		uniSearchBar,
		cuPanel,
		cuCell,
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			title: '费用类型',
			datas: [],
			searchDatas: [],
			reqData: {
				feecategoryname: '',
				status: 1
			}
		}
	},
	onLoad() {
	},
	onShow() {
		this.getFeeCategory()
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		},
		mainHeight() {
			return this.$mainIsSearchHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		getFeeCategory() {
			this.$refs.loading.open()
			queryFeeCategory(api.capFee).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.datas = res.data.data.resultList
					this.searchDatas = this.datas
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.returnMessage
					})
				}
			}).catch(error => {
				this.$refs.loading.close()
				uni.showToast({
					icon: 'none',
					title: error
				})
			})
		},
		handleClickContent(val) {
			if (val.status == 2) {
				uni.showToast({
					icon: 'none',
					title: '请先启用该类型'
				})
			} else {
				let pages = getCurrentPages()
				let prevPage = pages[pages.length - 2]
				prevPage.setData(
					{ 
						rData: val.feecategoryname
					}
				)
				uni.navigateBack({
					delta: 1
				})
			}
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					if (!item.feecategoryname) {
						item.feecategoryname = ''
					}
					return item.feecategoryname.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleShowPopup() {
			this.reqData = {
				feecategoryname: '',
				status: 1
			}
			this.$refs.popup.open()
		},
		handleStatusChange(val, item) {
			if (val.value) {
				this.$refs.loading.open()
				enableFeeCategory(api.capFee, item.feecategoryid).then(res => {
					this.$refs.loading.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.getFeeCategory()
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			} else {
				this.$refs.loading.open()
				disableFeeCategory(api.capFee, item.feecategoryid).then(res => {
					this.$refs.loading.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.getFeeCategory()
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			}
		},
		handleAdd() {
			if (this.reqData.feecategoryname) {
				this.$refs.loading.open()
				createFeeCategory(api.capFee, this.reqData).then(res => {
					this.$refs.loading.close()
					this.$refs.popup.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.getFeeCategory()
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请输入类型名称'
				})
			}
		},
		handleCancel() {
			this.$refs.popup.close()
		}
	}
}
